:root{--bg:#0f1226;--surface:#15193a;--surface-2:#1b2050;--primary:#5b8cff;--primary-700:#3f6ee6;--accent:#22d3ee;--ok:#10b981;--warn:#f59e0b;--danger:#ef4444;--text:#e6e8ff;--muted:#a8b0d6;--border:#293066;--radius:12px;--shadow:0 10px 30px rgba(0,0,0,.25);--font:"Inter",system-ui,-apple-system,Segoe UI,Roboto,Arial,sans-serif;}
*{box-sizing:border-box}html,body{height:100%}body{margin:0;background:var(--bg);color:var(--text);font-family:var(--font);-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}
a{color:var(--primary);text-decoration:none}a:hover{color:var(--accent)}
h1,h2,h3{margin:0 0 .6rem;line-height:1.15}h1{font-size:clamp(1.6rem,2.5vw,2.2rem);font-weight:700}h2{font-size:clamp(1.3rem,2vw,1.6rem);font-weight:600}
p{margin:.25rem 0 .9rem;color:var(--muted)}
.container{max-width:1200px;margin:0 auto;padding:24px}
.row{display:grid;gap:16px}.row.cols-2{grid-template-columns:repeat(2,minmax(0,1fr))}.row.cols-3{grid-template-columns:repeat(3,minmax(0,1fr))}
.card{background:linear-gradient(180deg,var(--surface),var(--surface-2));border:1px solid var(--border);border-radius:var(--radius);box-shadow:var(--shadow);padding:20px}
label{display:block;font-size:.9rem;color:var(--muted);margin-bottom:6px}
input,select,textarea{width:100%;background:#0e1230;color:var(--text);border:1px solid var(--border);border-radius:10px;padding:12px 14px;outline:none}
input::placeholder,textarea::placeholder{color:#8d95c2}
input:focus,select:focus,textarea:focus{border-color:var(--primary);box-shadow:0 0 0 3px rgba(91,140,255,.25)}
.btn{display:inline-flex;align-items:center;gap:8px;cursor:pointer;background:var(--primary);color:#fff;border:0;padding:10px 16px;border-radius:10px;font-weight:600;transition:.15s}
.btn.secondary{background:#232956;color:#d9dcff}.btn.ok{background:var(--ok)}.btn.warn{background:var(--warn)}.btn.danger{background:var(--danger)}
.table{width:100%;border-collapse:separate;border-spacing:0}
.table th,.table td{padding:12px 14px;border-bottom:1px solid var(--border);text-align:left}
.table th{color:#cfd4ff;font-weight:600;background:#12163a}
.table tr:hover td{background:rgba(255,255,255,.02)}
.topbar{position:sticky;top:0;z-index:20;background:rgba(10,12,28,.6);backdrop-filter:blur(10px);border-bottom:1px solid var(--border)}
.topbar .inner{display:flex;align-items:center;justify-content:space-between;gap:12px;padding:12px 20px}
.mt{margin-top:16px}.mb{margin-bottom:16px}
@media (max-width:920px){.row.cols-2,.row.cols-3{grid-template-columns:1fr}.container{padding:16px}}
